import React from 'react';
import {Link} from 'react-router-dom';
import './Quickdisplay.css'


const Displaysearch=(props)=>{

    const listmeal=({mealData})=>{
          if(mealData){
              return mealData.map((item)=>{
                  return(
                   <div>
                 
                 <div className='searchcontainer'>  
                    <Link to={`/detail/${item.mealtype}`}>

                        <div className='col-md-4'>{/*for 3 in 1 row*/}
                             
                            <div className="boxcontainer">
                                    <div className="boxcomponent1">
                                       <img src="https://i.postimg.cc/76kStDSv/food-wallpaper.png"/>
                                    </div>
                                    <div className="boxcomponent2">
                                       <div class="boxcomponentheading">
                                        {item.name}
                                       </div>
                                        <div className="boxcomponentsubheading">
                                            Start your day with exclusive breakfast option
                                         </div>
                                        
                                    </div>
                            </div>
                        </div>
                            
                    </Link>
                 </div>  
                 
            </div>
                )
            })
        }
    }

    return(
        <div>
            <div className="searchcontainer">
                <p className="searchheading">
                Quick searches
                </p>
                <p className="searchsubheading">
                    Discover resturants by type of meal
                </p>
                {listmeal(props)}
            </div>
           
            
        </div>
    )
}

export default Displaysearch;