import React from 'react';
import { BrowserRouter, Route} from 'react-router-dom';
import Home from './Home';
import Details from './Detail';
import Header from './Header';
import Resturant from './Resturantdetail';
import Form from './order/Forms';
import Orderdisplay from './order/Orderdisplay'

const Routing = () => {
    return(
        <BrowserRouter>
            <div>
                <Header/>
                <Route exact path="/" component={Home}></Route>
                <Route path="/detail/:id" component={Details}></Route>
                <Route path="/rest/:id" component={Resturant}></Route>
                <Route path="/placeorder/:id" component={Form}></Route>
                <Route path="/Orderdisplay" component={Orderdisplay}></Route>
            </div>
        </BrowserRouter>
    )
}

